<template>
  <div>
    <el-row>
      <el-col :span="2" :offset="6">
        
      </el-col>
      <el-col :span="10" :offset="0">
        <el-input v-model="uploadData.title" placeholder=""></el-input>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="2" :offset="6">
        
      </el-col>
      <el-col :span="10" :offset="0">
        <el-input v-model="uploadData.tag" placeholder=""></el-input>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="2" :offset="6">
        
      </el-col>
      <el-col :span="10" :offset="0">
        <el-input v-model="uploadData.source" placeholder=""></el-input>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="2" :offset="6">
        
      </el-col>
      <el-col :span="10" :offset="0">
        <el-input type="textarea"
                  :rows="3" v-model="uploadData.summary" placeholder=""></el-input>
      </el-col>
    </el-row>
    <el-row>
      <el-row>
        <el-col :span="2" :offset="6">
          av
        </el-col>
        <el-col :span="10" :offset="0">
          <el-input type="textarea"
                    :rows="3" v-model="uploadData.fdId" placeholder="av"></el-input>
        </el-col>
      </el-row>
      <el-col :span="12" :offset="6">
        <el-upload
          class="upload-demo"
          ref="upload"
          action="http://121.43.174.193:8080/myVideoWeb/videoController/add"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :before-remove="beforeRemove"
          :data="uploadData"
          :file-list="fileList"
          :auto-upload="false">
          <el-button slot="trigger" size="small" type="primary"></el-button>
          <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload"></el-button>
          <div slot="tip" class="el-upload__tip"></div>
        </el-upload>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'

  export default {
    name: 'Upload',
    data() {
      return {
        fileList: [],
        uploadData: {
          title: null,
          tag: null,
          source: null,
          summary: '',
          fdId: null
        }
      }
    },
    created: function () {
    },
    computed: mapGetters({}),
    components: {},
    methods: {
      submitUpload() {
        this.$refs.upload.submit();
      },
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      beforeRemove(file, fileList) {
        return this.$confirm(` ${ file.name }`);
      }
    },
    mounted() {
    }
  }
</script>

<style scoped>
</style>



